import axios from 'axios'
import configService from './config'

const mailService = {}

mailService.sendRegister = function(email,contraseña) {
  return new Promise(function(resolve, reject) {
    axios({
      method:'post',
      url: configService.apiUrl + '/sendRegister',
      dataType: "json",
      data: {
        email: email,
        contraseña: contraseña
      }
    })
    .then(function(res) {
      resolve(res)
    })
    .catch(function(err) {
      reject(err)
    })
  })
}

export default mailService