<template>
  <v-card width="500px">
    <v-card-title v-html="'Registro'" />
    <v-card-text>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >
        <v-text-field
          v-model="email"
          label="Email"
          type="email"
          dense
          outlined
          :rules="emailRules"
        />
        <v-text-field
          v-model="password"
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show1 ? 'text' : 'password'"
          label="Contraseña"
          dense
          outlined
          :rules="passwordRules"
          @click:append="show1 = !show1"
        />
        <v-text-field
          v-model="password2"
          :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show2 ? 'text' : 'password'"
          label="Confirmar contraseña"
          dense
          outlined
          :rules="passwordRules"
          @click:append="show2 = !show2"
        />
        <v-btn @click="register()" color="accent" block :loading="processing">Registrarse</v-btn>
        <v-divider class="my-4" />
        <v-btn @click="$emit('change-mode-login')" color="secondary" block>Volver</v-btn>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth'
import sendMailRegister from "../../services/SendMailService";

export default {
  name: "Registro",
  data () {
    return {
      processing: false,
      valid: true,
      email: '',
      password: '',
      password2: '',
      show1: false,
      show2: false,
      emailRules: [
        v => !!v || 'Email es requerido.',
        v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
      ],
      passwordRules: [
        v => !!v || 'Contraseña es requerido.',
        v => (this.password === this.password2) || 'Las contraseñas deben coincidir.'
      ]
    }
  },
  watch: {
    password () {
      this.$refs.form.validate()
    },
    password2 () {
      this.$refs.form.validate()
    }
  },
  methods: {
    async register () {
      this.$refs.form.validate()
      this.sendMail(this.email, this.password)
      if (this.valid) {
        this.processing = true
        try {
          const auth = getAuth()
          const registerUser = await createUserWithEmailAndPassword(auth, this.email, this.password)
          if(registerUser)  this.$toasted.success('Su registro fue exitoso', { duration: 5000 }), this.sendMail()
          else this.$toasted.error('Error en registro, intentalo de nuevo', { duration: 3000 })
        } catch (error) {
          (error)
          const errorCode = error.code
          if (errorCode === 'auth/email-already-in-use') this.$toasted.error('Correo ya esta en uso', { duration: 3000 })
          else this.$toasted.error('Error en autenticación', { duration: 3000 })
        }
        this.processing = false
      }
    },
    async sendMail(email,password) {
      await sendMailRegister.sendRegister(email,password)
    }
  }
}
</script>
